import './App.css';
import React, {useState} from "react";
import {Checkbox, FormControlLabel, FormGroup} from "@mui/material";
import data from './data.json'
import {PriceBlock} from "./PriceBlock";

function App() {
    const [selectedBox, setSelectedBox] = useState([])

    const checkboxClick = (e) => {
        if (e.target.checked) {
            selectedBox.push(+e.target.id)
            setSelectedBox([...selectedBox])
        } else {
            setSelectedBox(selectedBox.filter(el => el !== +e.target.id))
        }
    }

    return (
        <div className={'grid-container'}>
            <Box checkboxClick={checkboxClick}/>
            <PriceBlock disabled={selectedBox.length < 3} price={selectedBox}/>
        </div>
    );
}

const Box = React.memo((props) => {

    return (
        <div className={'checkbox-wrapper'}>
            <FormGroup>
                {data.map((el, i) =>
                    <FormControlLabel key={i}
                                      control={<Checkbox id={el.price} onClick={props.checkboxClick}/>}
                                      label={el.title}/>
                )}
            </FormGroup>
        </div>
    )
})
export default App;
