import {Box, Button, Modal, Typography} from "@mui/material";
import {useState} from "react";

export const PriceBlock = (props) => {
    const style = {
        position: 'absolute',
        top: '25%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };
    const [open, setOpen] = useState(false);
    const [price, setPrice] = useState(0)
    const handleClose = () => setOpen(false);

    const buttonClick = () => {
        setPrice(props.price.reduce((acc, el) => acc + el))
        setOpen(true)
    }
    return (
        <div className={'price_block'}>
            <Button disabled={props.disabled} onClick={buttonClick} variant="contained">Рассчитать</Button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                        Стоимость
                    </Typography>
                    <Typography id="modal-modal-description" sx={{mt: 2, fontSize: '20px'}}>
                        {(price * 0.7).toFixed(2) + ' BYN'}
                    </Typography>
                </Box>
            </Modal>
        </div>
    )
}